import React, { useRef, useState, useEffect, useMemo } from "react";
import "./App.css";
import { loadColorTable, loadColorCards } from "./utils/colorTable";
import { generatePixelArt } from "./utils/pixelArt";
import {
  Button,
  InputNumber,
  Upload,
  message,
  Spin,
  Checkbox,
  Divider,
  Select,
  Modal,
  Tabs,
} from "antd";

const { Option } = Select;

function App() {
  const [img, setImg] = useState(null);
  const [imgObj, setImgObj] = useState(null);
  const [pixelWidth, setPixelWidth] = useState(50);
  const [pixelHeight, setPixelHeight] = useState(50);
  const [cellSize, setCellSize] = useState(24);
  const [canvasUrl, setCanvasUrl] = useState("");
  const [loading, setLoading] = useState(false);
  const [lockRatio, setLockRatio] = useState(true);
  const [ratio, setRatio] = useState(1);
  const [colorMode, setColorMode] = useState("dominant");
  const [excludeEdge, setExcludeEdge] = useState(false);
  const [showText, setShowText] = useState(true); // 
  const [showReferenceLines, setShowReferenceLines] = useState(false); // 5
  const [colorTable, setColorTable] = useState([]); // 
  const [colorCards, setColorCards] = useState({}); // 
  const [showColorModal, setShowColorModal] = useState(false);
  const [selectedColors, setSelectedColors] = useState({}); //  { mard: ['A1', 'A2'], hama: ['H01'], ... }
  const [previewModalVisible, setPreviewModalVisible] = useState(false); // 
  const canvasRef = useRef();

  // 
  useEffect(() => {
    Promise.all([loadColorTable(), loadColorCards()]).then(([table, cards]) => {
      setColorTable(table);
      setColorCards(cards);
      // Mard
      if (cards.mard && cards.mard.length > 0) {
        const mardColorNames = cards.mard.map((c) => c.name);
        setSelectedColors({ mard: mardColorNames });
      } else {
        setSelectedColors({});
      }
    });
  }, []);

  // 
  const flattenedSelectedColors = useMemo(() => {
    const result = [];
    Object.keys(selectedColors).forEach((brand) => {
      const colorNames = selectedColors[brand] || [];
      colorNames.forEach((name) => {
        // colorTablebrand
        const color = colorTable.find((c) => c.brand === brand && c.name === name);
        if (color) {
          result.push(color);
        }
      });
    });
    return result;
  }, [selectedColors, colorTable]);

  // 
  const handleUpload = (info) => {
    let file = null;
    if (info.file && info.file.originFileObj) {
      file = info.file.originFileObj;
    } else if (info.file) {
      file = info.file;
    }
    if (!file) return;
    const url = URL.createObjectURL(file);
    setImg(url);
    const image = new window.Image();
    image.onload = () => {
      setImgObj(image);
      setRatio(image.width / image.height);
      if (lockRatio) {
        setPixelHeight(Math.round(pixelWidth / (image.width / image.height)));
      }
    };
    image.onerror = () => {
      message.error("");
      setImgObj(null);
    };
    image.src = url;
  };

  // 
  const handleWidthChange = (w) => {
    setPixelWidth(w);
    if (lockRatio && imgObj) {
      setPixelHeight(Math.round(w / ratio));
    }
  };
  // 
  const handleHeightChange = (h) => {
    setPixelHeight(h);
    if (lockRatio && imgObj) {
      setPixelWidth(Math.round(h * ratio));
    }
  };
  // 
  const handleLockRatio = (checked) => {
    setLockRatio(checked);
    if (checked && imgObj) {
      setPixelHeight(Math.round(pixelWidth / ratio));
    }
  };

  // 
  const handleGenerate = async () => {
    if (!imgObj) {
      message.error("");
      return;
    }
    setLoading(true);
    // 
    let useColors = colorTable;
    if (flattenedSelectedColors.length > 0) {
      useColors = flattenedSelectedColors;
    }
    setTimeout(() => {
      const { canvas } = generatePixelArt({
        img: imgObj,
        pixelWidth,
        pixelHeight,
        cellSize,
        colorTable: useColors,
        showGrid: true,
        colorMode,
        excludeEdge,
        showText,
        showReferenceLines,
      });
      setCanvasUrl(canvas.toDataURL("image/png"));
      canvasRef.current = canvas;
      setLoading(false);
    }, 100);
  };

  // 
  const handleDownloadImg = () => {
    if (!canvasRef.current) return;
    const link = document.createElement("a");
    link.href = canvasRef.current.toDataURL("image/png");
    link.download = ".png";
    link.click();
  };

  // 
  const handleOpenColorModal = () => {
    setShowColorModal(true);
  };

  // /
  const handleToggleBrand = (brand) => {
    const brandColors = colorCards[brand] || [];
    const brandColorNames = brandColors.map((c) => c.name);
    const currentSelected = selectedColors[brand] || [];
    const allSelected = brandColorNames.every((name) =>
      currentSelected.includes(name)
    );
    if (allSelected) {
      // 
      const newSelected = { ...selectedColors };
      delete newSelected[brand];
      setSelectedColors(newSelected);
    } else {
      // 
      setSelectedColors({
        ...selectedColors,
        [brand]: brandColorNames,
      });
    }
  };

  // 
  const isBrandFullySelected = (brand) => {
    const brandColors = colorCards[brand] || [];
    if (brandColors.length === 0) return false;
    const currentSelected = selectedColors[brand] || [];
    return brandColors.every((c) => currentSelected.includes(c.name));
  };

  // 
  const isBrandPartiallySelected = (brand) => {
    const brandColors = colorCards[brand] || [];
    if (brandColors.length === 0) return false;
    const currentSelected = selectedColors[brand] || [];
    const selectedCount = brandColors.filter((c) =>
      currentSelected.includes(c.name)
    ).length;
    return selectedCount > 0 && selectedCount < brandColors.length;
  };

  // 
  const getBrandSelectionInfo = () => {
    const brandNameMap = {
      mard: "Mard",
      hama: "Hama",
      perler: "Perler",
      "perler-mini": "Perler Mini",
      nabbi: "Nabbi",
      "artkal-s": "Artkal S",
      "artkal-r": "Artkal R",
      "artkal-c": "Artkal C",
      "artkal-a": "Artkal A",
    };
    
    const info = [];
    // 
    Object.keys(selectedColors).forEach((brand) => {
      const brandColors = colorCards[brand] || [];
      const currentSelected = selectedColors[brand] || [];
      const selectedCount = currentSelected.length;
      
      if (selectedCount > 0) {
        const brandDisplayName = brandNameMap[brand] || brand;
        const isFullySelected = isBrandFullySelected(brand);
        info.push({
          brand: brandDisplayName,
          count: selectedCount,
          total: brandColors.length,
          isFullySelected,
        });
      }
    });
    return info;
  };

  return (
    <div className="App">
      <h2 className="pretty-title"></h2>
      <Divider />
      <div className="pretty-section">
        <div className="pretty-label">1. </div>
        <Upload
          accept="image/*"
          showUploadList={false}
          beforeUpload={() => false}
          onChange={handleUpload}
        >
          <Button style={{ marginTop: 8, marginBottom: 8 }}></Button>
        </Upload>
        {img && (
          <div style={{ margin: "8px 0 0 0", textAlign: "center" }}>
            <img
              src={img}
              alt=""
              style={{ maxWidth: 180, maxHeight: 120 }}
            />
            {imgObj && (
              <div
                style={{
                  color: "#2c3e50",
                  fontSize: 13,
                  marginTop: 4,
                  fontFamily: "monospace",
                }}
              >
                {imgObj.width}  {imgObj.height}
              </div>
            )}
          </div>
        )}
      </div>
      <div className="pretty-section">
        <div className="pretty-label">2. </div>
        <div className="pretty-param-row">
          <span>():</span>
          <InputNumber
            min={2}
            max={200}
            value={pixelWidth}
            onChange={handleWidthChange}
          />
          <span style={{ marginLeft: 12 }}>():</span>
          <InputNumber
            min={2}
            max={600}
            value={pixelHeight}
            onChange={handleHeightChange}
          />
          <Checkbox
            checked={lockRatio}
            onChange={(e) => handleLockRatio(e.target.checked)}
            style={{ marginLeft: 16 }}
          >
            
          </Checkbox>
        </div>
        <div className="pretty-param-row">
          <span>(px):</span>
          <InputNumber
            min={8}
            max={64}
            value={cellSize}
            onChange={setCellSize}
          />
        </div>
        <div className="pretty-param-row">
          <span>:</span>
          <Select
            value={colorMode}
            onChange={setColorMode}
            style={{ width: 180 }}
          >
            <Option value="dominant"></Option>
            <Option value="average"></Option>
            <Option value="center"></Option>
            <Option value="original"></Option>
            <Option value="diagonal45">4/5</Option>
          </Select>
        </div>
        <div className="pretty-param-row">
          <Checkbox
            checked={excludeEdge}
            onChange={(e) => setExcludeEdge(e.target.checked)}
          >
            /
          </Checkbox>
        </div>
        <div className="pretty-param-row">
          <Checkbox
            checked={showText}
            onChange={(e) => setShowText(e.target.checked)}
          >
            
          </Checkbox>
        </div>
        <div className="pretty-param-row">
          <Checkbox
            checked={showReferenceLines}
            onChange={(e) => setShowReferenceLines(e.target.checked)}
          >
            5
          </Checkbox>
        </div>
        <div className="pretty-param-row">
          <Button onClick={handleOpenColorModal}></Button>
        </div>
        {Object.keys(selectedColors).length > 0 && (() => {
          const brandInfo = getBrandSelectionInfo();
          return (
            <div
              style={{
                marginTop: 8,
                marginBottom: 8,
                padding: "8px 12px",
                background: "#f2f5fb",
                border: "1px solid #5a6c7d",
                fontSize: "0.9rem",
                color: "#2c3e50",
                fontFamily: "zh-cn-full, sans-serif",
              }}
            >
              {brandInfo.map((info, index) => (
                <div key={index} style={{ marginBottom: index < brandInfo.length - 1 ? 4 : 0 }}>
                  <span style={{ fontWeight: 600 }}>{info.brand}</span>
                  <span>
                    {info.isFullySelected ? "" : `${info.count}`}
                  </span>
                </div>
              ))}
            </div>
          );
        })()}
      </div>
      <div
        className="pretty-section"
        style={{ textAlign: "center", marginTop: 18 }}
      >
        <Button
          type="primary"
          onClick={handleGenerate}
          disabled={!imgObj}
          loading={loading}
          style={{ minWidth: 120 }}
        >
          
        </Button>
      </div>
      <div style={{ margin: "32px 0 0 0", textAlign: "center" }}>
        {loading && <Spin />}
        {canvasUrl && (
          <div>
            <img
              src={canvasUrl}
              alt=""
              style={{
                maxWidth: "100%",
                margin: "0 auto",
                cursor: "pointer",
                display: "block",
              }}
              onClick={() => setPreviewModalVisible(true)}
            />
            <div
              style={{
                marginTop: 16,
                display: "flex",
                justifyContent: "center",
                gap: 12,
                flexWrap: "wrap",
              }}
            >
              <Button onClick={handleDownloadImg}></Button>
            </div>
          </div>
        )}
      </div>
      
      <Modal
        title=""
        open={showColorModal}
        onCancel={() => setShowColorModal(false)}
        onOk={() => setShowColorModal(false)}
        okText=""
        cancelText=""
        width={900}
        style={{ top: 20 }}
        bodyStyle={{ maxHeight: "70vh", overflowY: "auto" }}
      >
        <div style={{ marginBottom: 16 }}>
          <Button
            size="small"
            onClick={() => {
              // 
              const allSelected = {};
              Object.keys(colorCards).forEach((brand) => {
                allSelected[brand] = colorCards[brand].map((c) => c.name);
              });
              setSelectedColors(allSelected);
            }}
            style={{ marginRight: 8 }}
          >
            
          </Button>
          <Button
            size="small"
            onClick={() => setSelectedColors({})}
          >
            
          </Button>
        </div>
        <Tabs
          defaultActiveKey={Object.keys(colorCards)[0]}
          items={Object.keys(colorCards).map((brand) => {
            const brandColors = colorCards[brand] || [];
            const brandNameMap = {
              mard: "Mard",
              hama: "Hama",
              perler: "Perler",
              "perler-mini": "Perler Mini",
              nabbi: "Nabbi",
              "artkal-s": "Artkal S",
              "artkal-r": "Artkal R",
              "artkal-c": "Artkal C",
              "artkal-a": "Artkal A",
            };
            const brandDisplayName = brandNameMap[brand] || brand;
            const isFullySelected = isBrandFullySelected(brand);
            const isPartiallySelected = isBrandPartiallySelected(brand);

            return {
              key: brand,
              label: (
                <span>
                  {brandDisplayName} ({brandColors.length})
                </span>
              ),
              children: (
                <div style={{ padding: "16px 0" }}>
                  <div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      marginBottom: 16,
                      paddingBottom: 12,
                      borderBottom: "1px solid #f0f0f0",
                    }}
                  >
                    <Checkbox
                      indeterminate={isPartiallySelected}
                      checked={isFullySelected}
                      onChange={() => handleToggleBrand(brand)}
                      style={{ marginRight: 8 }}
                    >
                      <span style={{ fontWeight: 600, fontSize: 15 }}>
                         {brandDisplayName}
                      </span>
                    </Checkbox>
                  </div>
                  <Checkbox.Group
                    value={selectedColors[brand] || []}
                    onChange={(checkedValues) => {
                      setSelectedColors({
                        ...selectedColors,
                        [brand]: checkedValues,
                      });
                    }}
                    style={{ width: "100%" }}
                  >
                    <div
                      style={{
                        display: "flex",
                        flexWrap: "wrap",
                        gap: 8,
                      }}
                    >
                      {brandColors.map((color) => {
                        const hex = "#" + color.color.toUpperCase();
                        return (
                          <Checkbox
                            key={color.name}
                            value={color.name}
                            style={{
                              width: 130,
                              marginBottom: 4,
                              display: "flex",
                              alignItems: "center",
                            }}
                          >
                            <span
                              style={{
                                display: "inline-flex",
                                alignItems: "center",
                              }}
                            >
                              <span
                                style={{
                                  display: "inline-block",
                                  width: 18,
                                  height: 18,
                                  background: hex,
                                  border: "1px solid #5a6c7d",
                                  marginRight: 6,
                                  verticalAlign: "middle",
                                }}
                              />
                              <span
                                style={{
                                  color: "#2c3e50",
                                  fontFamily: "monospace",
                                  fontSize: 13,
                                }}
                              >
                                {color.name} {hex}
                              </span>
                            </span>
                          </Checkbox>
                        );
                      })}
                    </div>
                  </Checkbox.Group>
                </div>
              ),
            };
          })}
        />
      </Modal>
      <Modal
        title=""
        open={previewModalVisible}
        onCancel={() => setPreviewModalVisible(false)}
        footer={null}
        width="90%"
        style={{ top: 20 }}
        bodyStyle={{ textAlign: "center", padding: 20 }}
      >
        {canvasUrl && (
          <img
            src={canvasUrl}
            alt=""
            style={{ maxWidth: "100%", height: "auto" }}
          />
        )}
      </Modal>
    </div>
  );
}

export default App;
